import React from "react";
import { Select, Form, Input, Button } from 'antd';

import AuthService from "../../services/auth.service";

const { Option } = Select;

export const Register = () => {

    const onFinish = (values) => {
        console.log('Success:', values);
        AuthService.register(values).then(() => {
            window.location.reload();
        })
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    return (
        <Form
            name="basic"
            layout="vertical"
            initialValues={{
                remember: true,
            }}
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
            autoComplete="off"
        >
            <Form.Item
                label="Username"
                name="username"
                rules={[
                    {
                        required: true,
                        message: 'Please input username!',
                    },
                ]}
            >
                <Input/>
            </Form.Item>

            <Form.Item
                label="  (   )"
                name="appointment"
                rules={[
                    {
                        required: true,
                        message: 'Please input username!',
                    },
                ]}
            >
                <Input/>
            </Form.Item>

            <Form.Item
                label=""
                name="password"
                rules={[
                    {
                        required: true,
                        message: 'Please input password!',
                    },
                ]}
            >
                <Input.Password/>
            </Form.Item>

            <Form.Item
                label=""
                name="city"
            >
                <Select placeholder="" defaultValue="moscow">
                    <Option value="moscow"></Option>
                    <Option value="spb">-</Option>
                    <Option value="nn"> </Option>
                </Select>
            </Form.Item>

            <Form.Item>
                <Button type="primary" htmlType="submit">
                     
                </Button>
            </Form.Item>
        </Form>
    );
}
